export default class EventListener {
    constructor(event, executor){
        this._event = event.toString();
        this._executor = executor;
    }

    addTo(domElement){
        if (domElement.addEventListener) {
            domElement.addEventListener(this._event, this._executor);
        }
        else if (domElement.attachEvent) {
            domElement.attachEvent("on" + this._event, this._executor);
        }
    }

    removeFrom(domElement){
        if (domElement.removeEventListener) {
            domElement.removeEventListener(this._event, this._executor);
        }
        else if(domElement.detachEvent){
            domElement.detachEvent(this._event, this._executor);
        }
    }
}


