import _isString from 'lodash/isString'
export const ctrl = 'CTRL';
export const alt = 'ALT';
export const shift = 'SHIFT';

export default class KeyMatcher {

    constructor() {
        this._keys = [];
    }

    checkCtrl() {
        this._checkCtrl = true;
    }

    checkAlt() {
        this._checkAlt = true;
    }

    checkShift() {
        this._checkShift = true;
    }

    checkFor(key) {
        this._keys.push(key);
    }

    match(keyString) {
        //TODO: Use regex to filter whitespace.
        const keys = keyString.split('+');
        this._setKeysToMatch(keys);
        return this;
    }

    //KeyMatcher.match(string).with(param)
    //KeyMatcher.match(string).withEvent(event)
    //KeyMatcher.match(string).withString(string)

    //TODO:
    //KeyMatcher(string).matches(param)
    //KeyMatcher(string).matchesEvent(event)
    //KeyMatcher(string).matchesString(string);

    with(param) {
        if (_isString(param)) {
            this.withString(param)
        }
        else if (param instanceof KeyboardEvent) {
            return this.withEvent(param);
        }
    }

    withString(string) {

    }

    withEvent(event) {
        console.warn("shift", event.shiftKey, "alt", event.altKey, "ctrl", event.ctrlKey, "key", event.key);
        if (this._matchesKeys(event)) {
            return true;
        }
        else {
            return false;
        }
    }

    _matchesKeys(event) {
        // this._mustMatchCntrl(event.ctrlKey)
        let matchesAllKeys = false;
        for (let i = 0; i < this._keys.length; i++) {
            const key = this._keys[i];
            const matches = (key === event.key);
            matchesAllKeys = matches;
            if (!matches) {
                break
            }
        }
        return matchesAllKeys && this._shouldMatchCtrl(event.ctrlKey) && this._shouldMatchAlt(event.altKey) && this._shouldMatchShift(event.shiftKey);
    }

    _shouldMatchShift(pressedShift){
        return this._shouldMatchFunctionKey(pressedShift, this._checkShift)
    }

    _shouldMatchCtrl(pressedCtrl){
        return this._shouldMatchFunctionKey(pressedCtrl, this._checkCtrl)
    }

    _shouldMatchAlt(pressedAlt){
        return this._shouldMatchFunctionKey(pressedAlt, this._checkAlt)
    }

    _shouldMatchFunctionKey(pressedFunctionKey, shouldMatch){
        if(shouldMatch){
            return pressedFunctionKey === shouldMatch;
        }
        return true;
    }

    _setKeysToMatch(keys) {
        keys.forEach((key) => {
            switch (key) {
                case ctrl:
                    this.checkCtrl();
                    break;
                case alt:
                    this.checkAlt();
                    break;
                case shift:
                    this.checkShift();
                    break;
                default:
                    this.checkFor(key);
            }
        });
    }

    _mustMatchCntrl(boolean) {
        return boolean && this._checkCtrl
    }
}


